<template>
  <div>
    <div class="MyticketOrderDetailsWrap">
      <!---->
      <header class="MyticketOrderDetailsWrapHeader">
        <div class="MyticketOrderDetailsWrapHeaderBox">
          <div class="MyticketOrderDetailsWrapHeaderImageBox">
            <img src="../../assets/img/huileyouLogo.jpg" width="200" height="60">
          </div>
        </div>
      </header>
      <!---->
      <section class="MyticketOrderDetailsWrapContentBox">
        <div class="MyticketOrderDetailsWrapContent">
          <h4></h4>
          <div class="MyticketOrderDetailsWrapContentDetalis clearfix">
            <div class="MyticketOrderDetailsWrapContentDetalisLeft">
              <div class="MyticketOrderDetailsWrapContentDetalisLeftTop">
                <h5>{{ticketOrderDetail.tm_or_GoodsListName}}</h5>
                <div class="MyticketOrderDetailsWrapContentDetalisLeftParameter clearfix">
                  <!--<span>: </span>-->
                  <span>: {{ticketOrderDetail.tm_or_PayTime | getUseTime}}</span>
                  <!--<span>: {{ticketOrderDetail.ts_to_FromPlace}}</span>-->
                  <span>:  {{ticketOrderDetail.tm_or_SumPrice}}x{{ticketOrderDetail.tm_or_TicketCount}}</span>
                  <span v-show="ticketOrderDetail.ts_to_ChildCount">:  {{ticketOrderDetail.tm_or_SumPrice}}x{{ticketOrderDetail.tm_or_TicketCount}}</span>
                </div>
                <div class="AllMoney">
                  <strong>: {{ticketOrderDetail.tm_or_SumPrice*ticketOrderDetail.tm_or_TicketCount}}</strong>
                </div>
              </div>
              <div class="orderContacts">
                <h5></h5>
                <ul class="orderContactsData">
                  <li>: {{ticketOrderDetail.tm_or_ConnectName}}</li>
                  <li>: {{ticketOrderDetail.tm_or_TelePhone}}</li>
                  <li>: {{ticketOrderDetail.tm_or_Email}}</li>
                </ul>
              </div>
            </div>
            <div class="MyticketOrderDetailsWrapContentDetalisRight">
              <h2><i></i>: {{ticketOrderDetail.tm_or_OrderID}}</h2>
              <div class="MyticketOrderDetailsWrapContentDetalisRightContent">
                <p :style="{display:payStatus?'none':'block'}">{{ticketOrderDetail.tm_or_OutStatus | getOutStatus}}</p>
                <div class="pendingPayment">
                  <strong :style="{display:payStatus?'block':'none'}">{{ticketOrderDetail.tm_or_PayState | getTicketStatus}}</strong>
                  <span class="time" :style="{display:payStatus?'none':'block'}">20 </span>
                  <span class="pendingPaymentAllMoney" :style="{display:payStatus?'none':'block'}" >:{{ticketOrderDetail.tm_or_SumPrice*ticketOrderDetail.tm_or_TicketCount}}</span>
                  <!--<span class="pendingPaymentAllMoneyOut"><em>12960</em></span>-->
                  <el-button class="button" :style="{display:payStatus?'none':'block'}" @click="goPay(ticketOrderDetail)"></el-button>
                  <span class="cancelOrder" :style="{display:payStatus?'none':'block'}"  @click="cancelOrder(ticketOrderDetail.tm_or_OrderID)"></span>
                </div>
              </div>
              <div class="contactInformation">
                <img src="../../assets/img/center.png" width="50" height="50">
                <strong>-</strong>
                <span class="contactType"></span>
                <span class="contactTelephone">
                  
                  <em>400-161-9999  60339</em>
                  00:0024:00
                </span>
              </div>
            </div>
          </div>
        </div>
      </section>
    </div>
  </div>
</template>
<script>
  import {mapGetters} from 'vuex'

  export default {
    computed: Object.assign({},mapGetters([
      'ticketOrderDetail'
    ])),
    data() {
      return {
        payStatus:false
      }
    },
    created(){
      let status = JSON.parse(sessionStorage.getItem('payStatusObj'))
      this.payStatus = status.isOff;
    },

    methods: {
      initData() {
      },
      search() {
        this.initData()
      },
      //
      goPay(item){
        let orderInfo = {};
        orderInfo.oi_OrderName = item.tm_or_GoodsListName;
        orderInfo.OrderID = item.tm_or_OrderID;
        orderInfo.oi_SellMoney = item.tm_or_SumPrice*item.tm_or_TicketCount;
        orderInfo.oi_OrderTypeID = 2
        sessionStorage.setItem('orderInfo',JSON.stringify(orderInfo));
        this.$router.push({name:'PaymentPlatform'})
      },
      //
      cancelOrder(orderID){
        sessionStorage.setItem('orderTypeID',2);
        let deleteOptions = {
          "loginUserID": "huileyou",
          "loginUserPass": "123",
          "operateUserID": "",
          "operateUserName": "",
          "pcName": "",
          "orderID":orderID
        };
        this.$store.dispatch('DeleteOrder',deleteOptions)
        .then((suc)=>{
          this.$notify({
            message: suc,
            type: 'success'
          });
          this.$router.push({name:'MyTourOrder'});
        },err=>{
          this.$notify({
            message: err,
            type: 'error'
          });
        });
      }
    },
  }
</script>
<style scoped>
  .MyticketOrderDetailsWrapHeader {
    width: 100%;
    background-color: #fff;
    padding: 20px 0;
    border-bottom: 1px solid #f0f3f8;
  }

  .MyticketOrderDetailsWrapHeaderBox {
    width: 1190px;
    margin: 0 auto;
  }

  /**/
  .MyticketOrderDetailsWrapContentBox {
    /*position: absolute;*/
    /*top: 100px;*/
    /*left: 0;*/
    /*bottom: 0;*/
    /*right: 0;*/
    background-color: #f0f4fd;
    padding-bottom:100px;

  }

  .MyticketOrderDetailsWrapContent {
    width: 1190px;
    margin: 0 auto;
  }

  .MyticketOrderDetailsWrapContent h4 {
    font: 12px/4 "";
    color: #545d6c;
    margin-bottom: 10px;
  }

  .MyticketOrderDetailsWrapContentDetalisLeft {
    float: left;
    background-color: #fff;
    width: 965px;
  }

  .MyticketOrderDetailsWrapContentDetalisLeftTop {
    padding: 25px 40px;
  }

  .MyticketOrderDetailsWrapContentDetalisLeftTop h5 {
    font: bold 18px/28px "";
  }

  .MyticketOrderDetailsWrapContentDetalisLeftParameter {
    margin-top: 15px;
    font: 14px/2.5 "";
    padding-bottom: 22px;
    border-bottom: 1px dashed #ccc;
  }

  .MyticketOrderDetailsWrapContentDetalisLeftParameter span {
    width: 255px;
    float: left;
  }

  .AllMoney {
    font: 14px/57px "";
    color: #213f4a;
  }

  .orderContacts h5 {
    font: 18px/40px "";
    background-color: #f7f8fc;
    text-indent: 40px;
  }

  .orderContactsData {
    font: 14px/58px "";
    padding-left: 40px;
    color: #666;
  }

  .orderContactsData li {
    width: 255px;
    float: left;
  }

  .MyticketOrderDetailsWrapContentDetalisRight {
    float: right;
    width: 225px;
  }

  .MyticketOrderDetailsWrapContentDetalisRight h2 {
    text-align: center;
    font: 16px/60px "";
    background-color: #00da62;
    color: #fff;
    word-break:break-all;
    position: relative;
  }

  .MyticketOrderDetailsWrapContentDetalisRight h2 i {
    position: absolute;
    bottom: -12px;
    left: 50%;
    margin-left: -3px;
    border-bottom: 6px solid transparent;
    border-left: 6px solid transparent;
    border-right: 6px solid transparent;
    border-top: 6px solid #00da62;
  }

  .MyticketOrderDetailsWrapContentDetalisRightContent {
    border: 1px solid #eff0f4;
    background-color: #f4f7fc;
  }

  .MyticketOrderDetailsWrapContentDetalisRightContent p {
    font: 30px/3 "";
    color: #d75d36;
    text-align: center;
  }

  .contactInformation {
    background-color: #fff;
    padding: 18px;
    border: 1px solid #eff0f4;
    border-top: none;
  }

  .contactInformation > img {
    display: block;
    margin-bottom: 10px;
  }

  .contactInformation > strong {
    font: 14px/34px "";
    color: #2e2b36;
  }

  .contactType {
    font: 12px/32px "";
    color: #2e2b36;
    display: block;
    padding-left: 10px;
    margin-bottom: 10px;
  }

  .contactTelephone {
    font: 12px/20px "";
    color: #9a9b96;
    padding-left: 10px;
  }

  .contactTelephone em {
    display: block;
    color: #2e2b36;
  }

  .pendingPayment {
    text-align: center;
    padding: 20px 36px;
  }

  .pendingPayment > * {
    display: block;
  }

  .pendingPayment>strong {
    font:30px/2 "";
    color: #f60;
  }

  .time {
    display: block;
    margin-bottom: 20px;
    color: #999;
    font:12px/20px "";
    text-align: left;
  }

  .pendingPaymentAllMoney {
    font:16px/2 "";
  }

  .pendingPaymentAllMoneyOut {
    font:16px/2 "";
  }

  .pendingPaymentAllMoneyOut em {
    font-size: 18px;
    color: #f60;
  }

  .button {
    margin:0 auto;
    background-color: #f60;
    color: #fff;
  }

  .cancelOrder {
    font:18px/46px "";
    color: #999999;
  }
</style>



