import Comment from '@/components/public/Comment'//
import AgenciesDetail from '@/components/Agencies/AgenciesDetail'//
import AdminLogin from '@/components/AdminLogin'//
import Register from '@/components/Register'//
import AgenciesHome from '@/components/Agencies/AgenciesHome'//
import HeelTourSearchMore from '@/components/Agencies/HeelTourSearchMore' //
import MerchantRegister from '@/components/VendorRegistration/MerchantRegister'//
import Agents from '@/components/VendorRegistration/Agents'//

import FillInOrder from '@/components/Agencies/FillInOrder'//
import PersonalCenter from '@/components/PersonakCenterFolder/PersonalCenter'//
import MyTourOrder from '@/components/PersonakCenterFolder/MyTourOrder'//
import PersonalDataDetails from '@/components/PersonakCenterFolder/PersonalDataDetails'//
import UpdatePassword from '@/components/PersonakCenterFolder/UpdatePassword'//
import PaymentPlatform from '@/components/public/PaymentPlatform'//
import MyOrderQRCode from '@/components/MyOrderQRCode'//
import MyOrderDetails from '@/components/PersonakCenterFolder/MyOrderDetails'//
import TicketOrderDetails from '@/components/PersonakCenterFolder/TicketOrderDetails'//
import HotelOrderDetails from '@/components/PersonakCenterFolder/HotelOrderDetails'//
import MyCollection from '@/components/PersonakCenterFolder/MyCollection' //
import CommentOnAComment from '@/components/Agencies/CommentOnAComment' //

import MyPoints from '@/components/PersonakCenterFolder/MyPoints'//
//


import AdmissionTicketHomePage from '@/components/Tickets/AdmissionTicketHomePage'//
import TicketsDetail from '@/components/Tickets/TicketsDetail'//
import TicketsReserve from '@/components/Tickets/TicketsReserve'//
import AdmissionTicketMore from '@/components/Tickets/AdmissionTicketMore' //


import HotelHomePage from '@/components/Hotel/HotelHomePage'//
import HotelSearchMore from '@/components/Hotel/HotelSearchMore'//
import HotelDetalis from '@/components/Hotel/HotelDetalis'//
import HotelOrder from '@/components/Hotel/HotelOrder' //
import HotelParentChildTour from '@/components/Hotel/HotelParentChildTour'//
import HotelLovers from '@/components/Hotel/HotelLovers' //
import HotelMountaineering from '@/components/Hotel/HotelMountaineering'//
import HotelAncientTown from '@/components/Hotel/HotelAncientTown' //
import HotelInn from '@/components/Hotel/HotelInn'//
import HotelQingLv from '@/components/Hotel/HotelQingLv'//


export default [
  //
  {
    path: '/hotelQingLv',
    name: 'HotelQingLv',
    component: HotelQingLv
  },
  //
  {
    path: '/hotelAncientTown',
    name: 'HotelAncientTown',
    component: HotelAncientTown
  },
  //
  {
    path: '/hotelInn',
    name: 'HotelInn',
    component: HotelInn
  },
  //
  {
    path: '/hotelOrder',
    name: 'HotelOrder',
    component: HotelOrder
  },
  //
  {
    path: '/hotelMountaineering',
    name: 'HotelMountaineering',
    component: HotelMountaineering
  },
  //
  {
    path: '/hotelLovers',
    name: 'HotelLovers',
    component: HotelLovers
  },
  //
  {
    path: '/hotelParentChildTour',
    name: 'HotelParentChildTour',
    component: HotelParentChildTour
  },
  //
  {
    path: '/commentOnAComment/:id',
    name: 'CommentOnAComment',
    component: CommentOnAComment
  },
  //
  {
    path: '/paymentPlatform',
    name: 'PaymentPlatform',
    component: PaymentPlatform
  },
  //
  {
    path: '/ticketsReserve/:id',
    name: 'TicketsReserve',
    component: TicketsReserve
  },
  //
  {
    path: '/myOrderQRCode',
    name: 'MyOrderQRCode',
    component: MyOrderQRCode
  },
  //
  {
    path: '/myOrderDetails',
    name: 'MyOrderDetails',
    component: MyOrderDetails
  },
  //
  {
    path: '/ticketOrderDetails',
    name: 'TicketOrderDetails',
    component: TicketOrderDetails
  },
  //
  {
    path: '/hotelOrderDetails',
    name: 'HotelOrderDetails',
    component: HotelOrderDetails
  },
  //
  {
    path: '/merchantRegister',
    name: 'MerchantRegister',
    component: MerchantRegister
  },
  //
  {
    path: '/agents',
    name: 'Agents',
    component: Agents
  },
  //
  {
    path: '/personalCenter',
    name: 'PersonalCenter',
    component: PersonalCenter,
    children: [
      //
      {
        path: 'myTourOrder',
        components: {
          default: Comment,
          MyInformtion: MyTourOrder
        },
        name: 'MyTourOrder'
      },
      //
      {
        path: 'myPoints',
        components: {
          default: Comment,
          MyInformtion: MyPoints
        },
        name: 'MyPoints'
      },
      //
      {
        path: 'personalDataDetails',
        components: {
          default: Comment,
          MyInformtion: PersonalDataDetails
        },
        name: 'PersonalDataDetails'
      },
      //
      {
        path: 'updatePassword',
        components: {
          default: Comment,
          MyInformtion: UpdatePassword
        },
        name: 'UpdatePassword'
      },
      //
      {
        path: 'myCollection',
        components: {
          default: Comment,
          MyInformtion: MyCollection
        },
        name: 'MyCollection'
      }
    ]
  },
  //
  {
    path: '/fillInOrder',
    name: 'FillInOrder',
    component: FillInOrder
  },
  //
  {
    path: '/Register',
    name: 'Register',
    component: Register
  },
  //
  {
    path: '/Comment',
    name: 'Comment',
    component: Comment,
    children: [
      //
      {
        path: 'heelTourSearchMore',
        components: {
          default: Comment,
          User: HeelTourSearchMore
        },
        name: 'HeelTourSearchMore'
      },
      //
      {
        path: 'agenciesHome',
        components: {
          default: Comment,
          User: AgenciesHome
        },
        name: 'AgenciesHome'
      },
      //
      {
        path: 'agenciesDetail/:id',
        components: {
          default: Comment,
          User: AgenciesDetail
        },
        name: 'AgenciesDetail'
      },
      //
      {
        path: 'ticketsDetail/:id',
        components: {
          default: Comment,
          User: TicketsDetail
        },
        name: 'TicketsDetail'
      },
      //
      {
        path: 'admissionTicketHomePage',
        components: {
          default: Comment,
          User: AdmissionTicketHomePage
        },
        name: 'AdmissionTicketHomePage'
      },
      //
      {
        path: 'hotelHomePage',
        components: {
          default: Comment,
          User: HotelHomePage
        },
        name: 'HotelHomePage'
      },
      //
      {
        path: 'hotelSearchMore/:id',
        components: {
          default: Comment,
          User: HotelSearchMore
        },
        name: 'HotelSearchMore'
      },
      //
      {
        path: 'hotelDetalis/:id',
        components: {
          default: Comment,
          User: HotelDetalis
        },
        name: 'HotelDetalis'
      },
      //
      {
        path: 'admissionTicketMore/:id',
        components: {
          default: Comment,
          User: AdmissionTicketMore
        },
        name: 'AdmissionTicketMore'
      }
    ]
  },
  //
  {
    path: '/adminLogin',
    component: AdminLogin,
    name: 'AdminLogin'
  },
  {
    path: '/',
    hidden: true,
    redirect: {name: 'AgenciesHome'}
  },
  {
    path: '*',
    hidden: true,
    redirect: {name: 'AgenciesHome'}
  },
]



