<template>
  <div>
    <el-tabs type="border-card"  v-model="activeName2">
      <el-tab-pane label="" name="first">
        <h1 class="userClass"></h1>
        <el-col :span="24" class="formSearch" >
          <el-form :inline="true">
            <el-form-item>
              <span>:</span>
            </el-form-item>
            <el-form-item>
              <el-select v-model="typeID" placeholder="" size="small">
                <el-option
                  v-for="item in typeList"
                  :key="item.value"
                  :label="item.name"
                  :value="item.value">
                </el-option>
              </el-select>
            </el-form-item>
            <el-form-item>
              <el-button type="primary" @click="search" size="small"></el-button>
            </el-form-item>
          </el-form>
        </el-col>
        <el-table
          :data="useScoreDetailInfo"
          highlight-current-row
          v-loading="isLoading"
          style="width: 100%">
          <el-table-column
            prop="sm_ust_Name"
            label=""
            align="center"
          >
          </el-table-column>
          <el-table-column
            prop="sm_usd_CostPrice"
            align="center"
            label="()"
          >
          </el-table-column>
          <el-table-column
            align="center"
            prop="sm_usd_Score"
            label="">
          </el-table-column>
        </el-table>

        <!---->
        <div class="block" style="float: right;padding-top: 20px">
          <el-pagination
            @current-change="handleCurrentChange"
            :page-size="4"
            layout="total, prev, pager, next"
            :total="total"
            v-show="total"
          >
          </el-pagination>
        </div>
      </el-tab-pane>
      <el-tab-pane label=""  name="second">
        <h1 class="userClass"></h1>
        <el-col :span="24" class="formSearch" >
          <el-form :inline="true">
            <el-form-item>
              <span>:</span>
            </el-form-item>
            <el-form-item>
              <el-select v-model="allTypeID" placeholder="" size="small">
                <el-option
                  v-for="item in typeList"
                  :key="item.value"
                  :label="item.name"
                  :value="item.value">
                </el-option>
              </el-select>
            </el-form-item>
            <el-form-item>
              <el-button type="primary" @click="allSearch" size="small"></el-button>
            </el-form-item>
          </el-form>
        </el-col>

        <el-table
          :data="userAllScoreList"
          highlight-current-row
          v-loading="isLoading"
          style="width: 100%">
          <el-table-column
            prop="sm_ust_Name"
            label=""
            align="center"
          >
          </el-table-column>
          <el-table-column
            align="center"
            prop="sm_ss_Score"
            label="">
          </el-table-column>
        </el-table>
      </el-tab-pane>
    </el-tabs>
  </div>
</template>
<script>
  import {mapGetters} from 'vuex'
  export default{
    name: '',
    data(){
      return {
        activeName2: 'first',
        total:0,
        allTypeID:'',
        typeID:'',
        typeList:[
          {
            name:'',
            value:1
          },
          {
            name:'',
            value:3
          },
          {
            name:'',
            value:2
          },
          {
            name:'',
            value:4
          },
        ],
        user:{},
        isLoading:false,
      }
    },
    computed: mapGetters([
      'useScoreDetailInfo',
      'userAllScoreList'
    ]),
    created(){
      //
      this.user = JSON.parse(sessionStorage.getItem('user'));
      if(this.user){
        this.initData(1,this.user.ui_UserCode,'')
      }
    },
    methods: {
      //
      handleCurrentChange(num){
        this.initData(num,this.user.ui_UserCode,this.typeID)
      },
      //
      initData(page,userCode,typeID){
        let options = {
          "loginUserID": "huileyou",//
          "loginUserPass": "123",//
          "operateUserID": "",//
          "operateUserName": "",//
          "pcName": "",//
          "userCode": userCode?userCode:'',//
          "scoreTypeID": typeID,// 0  1 2 3
          "page":page?page:1,
          "rows":4
        };
        this.isLoading = true;
        this.$store.dispatch('SelectUseScoreDetailInfo',options)
        .then((total)=>{
          this.total = total;
          this.isLoading = false;
        },err=>{
          this.$notify({
            message: err,
            type: 'error'
          });
        })
      },
      //
      initAllData(userCode,allTypeID){
        let allOptions = {
          "loginUserID": "huileyou",//
          "loginUserPass": "123",//
          "operateUserID": "",//
          "operateUserName": "",//
          "pcName": "",//
          "userCode": userCode?userCode:'',//
          "scoreTypeID": allTypeID,// 1 2 3 4
        }
        this.isLoading = true;
        this.$store.dispatch('selectUserAllScore',allOptions)
        .then(()=>{
          this.isLoading = false;
        },err=>{
          this.$notify({
            message: err,
            type: 'error'
          });
        })
      },
      //
      search(){
        this.initData(1,this.user.ui_UserCode,this.typeID)
      },
      //
      allSearch(){
        this.initAllData(this.user.ui_UserCode,this.allTypeID)
      }
    },
  }
</script>
<style scoped>
  .userClass {
    padding: 20px 0 0 20px;
    font-size: 18px;
  }
  .formSearch {
    padding: 20px 0 0 20px;
  }
</style>



