  .screeningRecommendType > a {
    float: left;
    width: 100px;
    text-align: center;
    border-right: 1px solid #eee;
    color: #666;
  }

  .screeningRecommendType > a.active {
    background-color: #38c249;
    color: #fff;
    font-weight: bold;
  }

  .searchType {
    padding: 10px 0 10px 30px;
  }

  .accordingToData {
    margin-top: 20px;
  }

  .accordingToData > li {
    float: left;
    width: 100%;
    padding: 5px;
    border: 1px solid #ccc;
    margin-bottom: 10px;
  }

  .accordingToData > li > div {
    float: left;
  }

  .accordingToDataBox {
    width: 200px;
    position: relative;
    margin-top: 5px;
  }

  .accordingToDataBox > span {
    position: absolute;
    top: 0;
    left: 0;
    font: 12px/18px "";
    padding: 0 3px;
    background-color: #2dbb55;
    color: #fff;
  }

  .accordingToDataContent {
    width: 450px;
    margin-left: 15px;
  }

  .accordingToDataContent > h4 {
    font: 16px/24px "";
  }

  .accordingToDataContent > h4 > span {
    color: #ff0000;
  }

  .accordingToDataContent > h4 > strong {
    color: #2e9700;
  }

  .activeTypeList {
    font: 12px/16px "";
    margin: 5px 0;
  }

  .activeTypeList > li {
    float: left;
    border: 1px solid #ffb622;
    padding: 0 3px;
    -webkit-border-radius: 3px;
    -moz-border-radius: 3px;
    border-radius: 3px;
    margin-right: 10px;
    color: #ff8800;
  }

  .accordingToData > li:hover {
    cursor: pointer;
    background-color: #fffff5;
  }

  .accordingToData > li:hover h4 strong {
    color: #ff0000;
  }

  .priceRecommend {
    width: 200px;
  }

  .describeWrap {
    overflow: hidden;
    font: 14px/20px "";
  }

  .goPlace {
    color: #2e9700;
  }

  .describe {
    color: #666;
  }

  .containsAttractions {
    white-space: nowrap;
  }

  .goTimeA {
    display: block;
    white-space: nowrap;
  }

  .priceRecommend {
    margin-left: 20px;
  }

  .priceRecommend > strong {
    font: 12px/3 "";
    text-align: right;
    display: block;
    color: #f80;
  }

  .priceRecommend > strong > em {
    font-size: 20px;
  }

  .priceRecommend > strong > span {
    color: #666666;
  }

  .satisfactionBox {
    background-color: #f6f6f6;
    border: 1px solid #eeeeee;
    -webkit-border-radius: 5px;
    -moz-border-radius: 5px;
    border-radius: 5px;
  }

  .satisfactionBox > div {
    float: left;
    padding: 10px;
    font: 14px/1.3 "";
  }

  .satisfactionBox > div:first-of-type {
    width: 70px;
    border-right: 1px dashed #dddddd;
  }

  .satisfactionBox > div:nth-of-type(2) {
    width: 128px;
    color: #999999;
  }

  .satisfactionNumber {
    color: #999999;
  }

  .satisfactionNumber > strong {
    color: #666666;
    font-weight: bold;
  }

  .satisfactionBox > div:nth-of-type(2) em {
    color: #666666;
  }

  .HeelTourSearchMoreWrapRecommend {
    float: right;
    width: 255px;
  }

  .recommendSatisfaction {
    font: 12px/2 "";
    color: #666;
  }

  .satisfaction {
    color: #f80;
  }

  .satisfaction em {
    font-size:20px;
    font-weight: bold;
  }



</style>



