<script>
  import {mapGetters} from 'vuex'

  export default {
    computed: mapGetters([
      'themeTypeList',
      'roomHardList',
      'searchData'
    ]),
    data() {
      return {
        total:0,
        checked:false,
        isLoading:false,
        roomHardRadio:'',
        themeTypeRadio:'',
        value1: '',
        value2: '',
        n: 0,
        destinationTypeList: [
          '',
          '',
          'ABC',
          'DEFG',
          'HJK',
          'LMN',
          'PQRS',
          'TWX',
          'YZ'
        ],
        hotCityList: [
          '',
          '',
          '',
          '',
          '',
          '',
          '',
          '',
          '',
          '',
          '',
          '',
          '',
          '',
          '',
          '',
          '',
          ''
        ],
        AllCityList: [
          '',
          '',
          '',
          '',
          '',
          '',
          '',
          '',
          '',
          '',
          ''
        ],
        AdministrativeArea: '',
        isAdministrativeArea: false,
        HotelRecommendDetalisNav: [
          '',
          '',
          '',
          ''
        ],
        m: 0,
        searchOptions:{
          "loginUserID": "huileyou",
          "loginUserPass": "123",
          "operateUserID": "",
          "operateUserName": "",
          "pcName": "",
          //"ht_it_ID":"1",//
          "ht_tt_ID":"",//
          //"ht_hd_ID":"1",//
          sm_af_AreaID:'',
          "ht_rh_ID":"",//
          "page":"1", //   1
          "rows":"5",//
        }
      }
    },
    created(){
      let id= this.$route.params.id;
      this.searchOptions.sm_af_AreaID = id;
      //
      this.$store.commit('showLoading')
      this.initData().then(()=>{
        this.$store.commit('hideLoading')
        this.searchDataList(this.searchOptions)
      })
    },
    methods: {
      //
      searchMap(hotelModel) {
        var map = new BMap.Map("allmap");
        var point = new BMap.Point(hotelModel.ht_ht_Longitude, hotelModel.ht_ht_Latitude);
        var marker = new BMap.Marker(point);  // 
        map.addOverlay(marker);              // 
        map.centerAndZoom(point, 20);
        setTimeout(function(){
          map.setZoom(14);
        }, 2000);  //214
        map.enableScrollWheelZoom(true);
        var opts = {
          width : 200,     // 
          height: 100,     // 
          title : hotelModel.ht_ht_HotelName , // 
          enableMessage:true,//
          message:"~"
        };
        var infoWindow = new BMap.InfoWindow(`${hotelModel.ht_ht_HotelAddress}`, opts);  // 
        marker.addEventListener("click", function(){
          map.openInfoWindow(infoWindow,point); //
        });
      },
      //
      lookMap(item){
        this.searchMap(item)
      },
      //
      handleCurrentChange(num){
        this.searchOptions.page = num;
        this.searchDataList(this.searchOptions)
      },
      //
      changeThemeType(id){
        this.searchOptions.ht_tt_ID = id;
        this.searchDataList(this.searchOptions)
      },
      //
      changeRoomHard(id){
        this.searchOptions.ht_rh_ID = id;
        this.searchDataList(this.searchOptions)
      },
      //
      changeRoomHardAll(){
        this.roomHardRadio = '';
        this.searchOptions.ht_rh_ID = ''
        this.searchDataList(this.searchOptions)
      },
      changeThemeHardAll(){
        this.themeTypeRadio = '';
        this.searchOptions.ht_tt_ID = ''
        this.searchDataList(this.searchOptions)
      },
      searchDataList(options){
        this.isLoading = true;
        this.$store.dispatch('initSearchData',options)
        .then((total)=>{
          this.total = total;
          this.isLoading = false;
        })
      },
      async initData(){
        //
        let initHotelSearchFilterInfo = {
          "loginUserID": "huileyou",
          "loginUserPass": "123",
          "operateUserID": "",
          "operateUserName": "",
          "pcName": "",
        };
        await this.$store.dispatch('initHotelSearchFilterInfo',initHotelSearchFilterInfo)

        //
//        await this.$store.dispatch('initSearchData',this.searchOptions)
      },
      goHotelDetails(item){
        this.$router.push({name:'HotelDetalis', params: {id: item.ht_ht_hotelID}})
      },
      changeCity() {

      },
      changeClass(index) {
        this.n = index;
      },
      isDisplay() {
        if (this.AdministrativeArea == '') {
          this.isAdministrativeArea = true;
          this.AdministrativeArea = 'active';
        } else {
          this.isAdministrativeArea = false;
          this.AdministrativeArea = '';
        }

      },
      changeType(index) {
        this.m = index;
      }
    },
  }
</script>
