    width: 179px;
    border-left:1px dashed #ccc;
    margin-top:10px;
    height: 140px;
  }

  .HotelRecommendPriceMoney {
    font: 14px/38px "";
    color: #f60;
    display: block;
    text-align: center;
  }

  .HotelRecommendPriceMoney > span {
    font-size: 25px;
    font-weight: bold;
  }

  .HotelRecommendPrice > .scoreDetails {
    transform: translateX(65px);
  }

  .HotelRecommendPrice > .scoreDetails > span {
    float: left;
    font: 12px/20px "";
    color: #999;
  }

  .scoreDetailsContent {
    float: left;
    position: relative;
    margin-left: 3px;
  }

  .scoreDetailsContent:hover div {
    display: block;
  }

  .scoreDetailsContent:hover i {
    cursor: pointer;
  }

  .scoreDetailsContent > div {
    display: none;
    position: absolute;
    left: -7px;
    top: 20px;
    width: 280px;
    font: 16px/30px "";
    padding: 8px 22px;
    background-color: #fff;
    border: 1px solid #ffdbb2;
    color: #999999;
    -webkit-border-radius: 5px;
    -moz-border-radius: 5px;
    border-radius: 5px;
  }

  .scoreDetailsContent > div::before {
    content: '';
    position: absolute;
    width: 10px;
    height: 10px;
    border-left: 1px solid #ffdbb2;
    border-top: 1px solid #ffdbb2;
    top: -5px;
    left: 10px;
    transform: rotate(45deg);
    background-color: #fff;
  }

  .scoreDetailsContent > div em {
    color: #fe9010;
  }

  .scoreDetailsContent > div > span {
    float: left;
  }

  .scoreDetailsContent > div > span:nth-of-type(2n) {
    margin-left: 30px;
  }

  .scoreDetailsContent > i {
    display: block;
    width: 15px;
    height: 16px;
    background: url("../../assets/img/hotelDatelisIcon.png") no-repeat -75px -49px;
  }

  .commentNumber {
    display: block;
    text-align: center;
    font: 14px/22px "";
    color: #cccccc;
    margin-bottom: 4px;
  }

  .commentNumber > em {
    color: #2dbb55;
  }

  .HotelRecommendPrice > a {
    width: 100px;
    font: bold 14px/2.3 "";
    background-color: #f60;
    color: #fff;
    text-align: center;
    margin:0 auto;
    -webkit-border-radius: 5px;
    -moz-border-radius: 5px;
    border-radius: 5px;
  }
    /**/
  .HotelRecommendMap {
    float: right;
  }


</style>



