<script>
  import {mapGetters} from 'vuex'

  export default {

    data() {
      return {
        centerDialogVisible:false,
        total:0,
        begin:'',
        end:'',
        isLoading:false,
        value:'',
        showList: [
          {
            id: 0,
            isShow: true
          },
          {
            id: 1,
            isShow: false
          },
          {
            id: 2,
            isShow: false
          },
          {
            id: 3,
            isShow: false
          },
          {
            id: 4,
            isShow: false
          }
        ],
        n: 0,
        hotelInfromationNav: [
          '',
          '',
          '',
          '707',
          ''
        ],
        s:111,
        value1: '',
        value2: '',
        checked: false,
        value5: 4.7,
        scoreType: [
          '',
          '',
          ''
        ],
        partitionList: [
          '',
          '',
          '',
          ''
        ],
      }
    },
    computed: mapGetters([
      'hotelDetalis',
      'hotelModel',
      'hotelPolicy',
      'hotelRoom',
      'hotelImage',
      'exteriorList',
      'publicAreasList',
      'theLobbyList',
      'restaurantList',
      'searchHotelRoomFilterObj'
    ]),
    created(){
      let id= this.$route.params.id;
      this.initData(id).then(()=>{
        this.initRoomSearch().then(()=>{
          this.searchRoomInfo()
        })
        this.searchMap(this.hotelModel);
      })
    },
    methods:{
      //
      centerDialogVisibleSubmit() {
        this.$router.push({name: 'AdminLogin'});
      },
      //
      handleCurrentChange(num){
        this.searchRoomInfo({
          page:num
        })
      },
      //
      submit(){
        this.searchRoomInfo({
          inDate:this.begin,
          outDate:this.end
        })
      },
      //
      clickBedType(item){
        this.searchRoomInfo({
          inDate:this.begin,
          outDate:this.end,
          ht_rpp_BedType:item.ht_rpp_BedType
        })
      },
      //
      clickBreakfastType(item){
        this.searchRoomInfo({
          inDate:this.begin,
          outDate:this.end,
          ht_rpp_BreakfastType:item.ht_rpp_BreakfastType
        })
      },
      //
      clickWafiType(item){
        this.searchRoomInfo({
          inDate:this.begin,
          outDate:this.end,
          ht_rpp_WafiType:item.ht_rpp_WafiType
        })
      },
      //
      clickCancelType(item){
        this.searchRoomInfo({
          inDate:this.begin,
          outDate:this.end,
          ht_rpp_CancelType:item.ht_rpp_CancelType
        })
      },
      searchRoomInfo(obj){
        let options = {
          "loginUserID": "huileyou",
          "loginUserPass": "123",
          "operateUserID": "",
          "operateUserName": "",
          "pcName": "",
          "page": "1",
          "rows": "5",
          "ht_bt_HotelID": this.$route.params.id,  //ID 
          "inDate": "", //
          "outDate": "",//
          "ht_rpp_BedType": "", //
          "ht_rpp_BreakfastType": "", //
          "ht_rpp_WafiType": "",//wafi
          "ht_rpp_CancelType": "",//
        }
        this.isLoading = true;
        for(let attr in obj){
          options[attr] = obj[attr]
        }
        this.$store.dispatch('initSearchRoomInfo',options)
        .then((total)=>{
          this.total = total;
          this.isLoading = false
        },err=>{
          this.$notify({
            message: err,
            type: 'error'
          });
          this.isLoading = false
        })
      },
      //
      lookSearchDetils(index){
        this.s = index
      },
      //
      closeSearchDetils(index){
        if(this.s==index){
          this.s = 111
        }
      },
      initRoomSearch(){
        let options = {
          "loginUserID": "huileyou",
          "loginUserPass": "123",
          "operateUserID": "",
          "operateUserName": "",
          "pcName": "",
        }
        return this.$store.dispatch('SearchHotelRoomFilter',options)
      },
      async initData(id){
        let options = {
          "loginUserID": "huileyou",
          "loginUserPass": "123",
          "operateUserID": "",
          "operateUserName": "",
          "pcName": "",
          "ht_ht_hotelID": id,//ID
          //"sm_ai_AgentInfoID": "10",//ID     ID
        };
        await this.$store.dispatch('initHotelDetalis',options)
      },
      //
      menuClick(index) {
        this.n = index;
        for (var i = 0; i < this.showList.length; i++) {
          if (this.showList[i].id == index) {
            this.showList[i].isShow = true
          } else {
            this.showList[i].isShow = false
          }
        }
      },
      //
      searchMap(hotelModel) {
        var map = new BMap.Map("allmap");
        var point = new BMap.Point(hotelModel.ht_ht_Longitude, hotelModel.ht_ht_Latitude);
        var marker = new BMap.Marker(point);  // 
        map.addOverlay(marker);              // 
        map.centerAndZoom(point, 15);
        var opts = {
          width : 200,     // 
          height: 100,     // 
          title : hotelModel.ht_ht_HotelName , // 
          enableMessage:true,//
          message:"~"
        };
        var infoWindow = new BMap.InfoWindow(`${hotelModel.ht_ht_HotelAddress}`, opts);  // 
        marker.addEventListener("click", function(){
          map.openInfoWindow(infoWindow,point); //
        });
      },
      //
      goHotelOrder(v,item) {
        let user = sessionStorage.getItem('user');
        if(!user){
          this.centerDialogVisible = true;
          return;
        }
        v.RoomInfo = item.RoomInfo;
        v.hotelName = this.hotelModel.ht_ht_HotelName;
        v.hotelAddress = this.hotelModel.ht_ht_HotelAddress;
        v.sm_ai_AgentInfoID=this.hotelModel.sm_ai_AgentInfoID;
        sessionStorage.setItem('hotelOrderReservations',JSON.stringify(v));
        this.$router.push({name: 'HotelOrder'})
      }
    },
  }
</script>
