<template>
  <div>
    <div class="huiLeYouCashierWrap">
      <!---->
      <header class="huiLeYouCashierWrapHeader">
        <div class="huiLeYouCashierWrapHeaderContent clearfix">
          <div class="huiLeYouCashierWrapHeaderContentFloat">
            <strong @click="toPay">{{user.ui_Name}}</strong>
            <span></span>
            <a href="javaecript:;"></a>
          </div>
        </div>
      </header>
      <!---->
      <section class="huiLeYouCashierWrapContent">
        <!--logo-->
        <div class="huiLeYouCashierWrapContentTitle clearfix">
          <img src="../../assets/img/center.png" width="70" height="50">
          <strong></strong>
        </div>
        <!---->
        <div class="productContentDetails clearfix" v-if="orderInfo.title">
          <div class="productContentDetailsContent">
            <strong>: {{orderInfo.title}}</strong>
            <ul class="aboutPayment clearfix">
              <li>: {{orderInfo.orderID}} <span></span></li>
              <li>: {{orderInfo.adultNumber*orderInfo.adultPrice+orderInfo.childNumber*orderInfo.childPrice}}.00 </li>
              <!--<li>: 3434.00</li>-->
            </ul>
          </div>
          <div class="paymentMoney">
            <strong>: <span>{{orderInfo.adultNumber*orderInfo.adultPrice+orderInfo.childNumber*orderInfo.childPrice}}.00</span></strong>
          </div>
        </div>
        <!---->
        <div class="productContentDetails clearfix" v-else>
          <div class="productContentDetailsContent">
            <strong>: {{orderInfo.oi_OrderName}}</strong>
            <ul class="aboutPayment clearfix">
              <li>: {{orderInfo.OrderID}} <span></span></li>
              <li>: {{orderInfo.oi_SellMoney+''}}.00 </li>
              <!--<li>: 3434.00</li>-->
            </ul>
          </div>
          <div class="paymentMoney">
            <strong>: <span>{{orderInfo.oi_SellMoney}}.00</span></strong>
          </div>
        </div>

        <!---->
        <div class="paymentPlatform">
          <h4></h4>
          <ul class="paymentPlatformList clearfix">
            <li><a href="javascript:;" @click="wechatPay"></a></li>
          </ul>
        </div>
      </section>
      <!---->
      <footer class="commonProblem">
        <h4>:</h4>
        <ul class="commonProblemList">
          <li>
            <strong>1.</strong>
            <span>2</span>
          </li>
          <li>
            <strong>
              <strong>2.</strong>
              <span></span>
            </strong>
          </li>
          <li>
            <strong>3.</strong>
            <span>4007-999-999</span>
          </li>
          <li>
            <strong>4</strong>
            <span></span>
          </li>
          <li>
            <strong>5.</strong>
            <span>
              1<br/>
              2<br/>
              34007-999-999
            </span>
          </li>
          <li>
            <strong>6</strong>
            <span>4007-999-999</span>
          </li>
          <li>
            <strong>7</strong>
            <span>
              1<br/>
              2<br/>
              3<br/>
              4<br/>
              5
            </span>
          </li>
        </ul>
      </footer>
    </div>
  </div>
</template>
<script>
  import {mapGetters} from 'vuex'

  export default {
    computed: mapGetters([]),
    data() {
      return {
        user:{},
        orderInfo:{}

      }
    },
    created(){
      let user = JSON.parse(sessionStorage.getItem('user'))
      let orderInfo = JSON.parse(sessionStorage.getItem('orderInfo'))
      orderInfo.oi_SellMoney = parseInt(orderInfo.oi_SellMoney)
      orderInfo.adultPrice = parseInt(orderInfo.adultPrice)
      orderInfo.childPrice = parseInt(orderInfo.childPrice)

      if(user){
        this.user = user
      }else{
        this.$router.push({name:'AdminLogin'})
        return
      }
      if(orderInfo){
        this.orderInfo = orderInfo
      }else{
        switch (orderInfo.oi_OrderTypeID){
          case 0:
            //
            this.$router.push({name:'HueiLeYouTourHomePage'})
            break;
          case 2:
            //
            this.$router.push({name:'TicketsDetail'})
            break;
          case 4:
            //
            this.$router.push({name:'HotelSearchMore'})
            break;
        }
      }
    },
    methods: {
      initData() {
      },
      //
      toPay(){
        this.$router.push({name:'MyTourOrder'})
      },
      search() {
        this.initData()
      },
      //
      wechatPay(){
        this.$router.push({name:'MyOrderQRCode'})
      }
    },
  }
</script>
<style scoped>
  .huiLeYouCashierWrap {
    background-color: #f0f0f0;
    width:100%;
    padding-bottom:30px;
  }

  .huiLeYouCashierWrapHeader {
    width: 100%;
    background-color: #fff;
    border-bottom: 1px solid #e2e2e2;
  }

  .huiLeYouCashierWrapHeaderContent {
    width: 1200px;
    margin: 0 auto;
  }

  .huiLeYouCashierWrapHeaderContentFloat {
    float: right;
    font: 14px/30px "";
    color: #666052;
  }

  .huiLeYouCashierWrapHeaderContentFloat strong {
    float: left;
    padding: 0 20px;
  }

  .huiLeYouCashierWrapHeaderContentFloat span {
    float: left;
    width: 2px;
    height: 15px;
    background-color: #d2d2d2;
    margin-top: 7px;
  }

  .huiLeYouCashierWrapHeaderContentFloat a {
    color: #666052;
    float: left;
    padding: 0 20px;
  }

  .huiLeYouCashierWrapContent {
    width: 1200px;
    margin: 0 auto;
  }

  .huiLeYouCashierWrapContentTitle {
    padding: 20px 0;
  }

  .huiLeYouCashierWrapContentTitle img {
    float: left;
    padding-right: 20px;
    border-right: 1px solid #fff;
  }

  .huiLeYouCashierWrapContentTitle strong {
    float: left;
    font: 24px/2 "";
    color: #676566;
    padding-left: 20px;
  }

  .productContentDetails {
    padding: 20px;
    background-color: #fff;
    font: 14px/2 "";
    color: #432e1b;
  }

  .productContentDetailsContent {
    width: 700px;
    float: left;
  }

  .paymentMoney {
    float: right;
  }

  .paymentMoney span {
    font-size: 24px;
    color: #f95053;
  }

  .aboutPayment li {
    float: left;
  }

  .aboutPayment {
    overflow: hidden;
  }

  .aboutPayment li span {
    display: inline-block;
    width: 2px;
    height: 15px;
    background-color: #7490a8;
    margin: 0 5px;
  }

  .paymentPlatform {
    margin-top: 20px;
    background-color: #fff;
    padding: 20px 40px;
  }

  .paymentPlatform h4 {
    font: 18px/2 "";
    color: #878787;
  }

  .paymentPlatformList {
    margin-top: 10px;
    font: 14px/24px "";
  }

  .paymentPlatformList li {
    float: left;
  }

  .paymentPlatformList > li > a {
    color:#8d8d8d;
  }
  .paymentPlatformList > li > a:hover{
    color: #f60;
  }

  .commonProblem {
    width:1200px;
    margin: 20px auto;
  }

  .commonProblem>h4 {
    font: 18px/4 "";
    color: #666;
  }

  .commonProblemList {
    font: 14px/24px "";
  }

  .commonProblemList > li {
    margin-top: 20px;
  }

  .commonProblemList strong {
    display: block;
  }

  .commonProblemList span {
    display: block;
  }
</style>



